import oracledb from 'oracledb'

import 'babel-polyfill'

describe('Oracle db connection', ()=>{
	
	it('connect', (done)=>{
		oracledb.getConnection({
			user: 'tester',
			password: '1234',
			connectString: '172.18.0.2:1521/xe',
			connectionString: 'jdbc:oracle:thin:tester:1234:@172.18.0.2:1521/xe', 
		}, async (err, connection)=>{
			console.log(err)
			connection.execute('select * from dual', [], (err,result)=>{
				console.log(result)
				connection.release()
				done()
			})	
			
		})
	})

})